<template>
  <el-card shadow="hover">
    <div slot="header" style="position: relative">
      <el-page-header content="帮助手册" @back="$router.push('/index')"></el-page-header>
    </div>
    <el-container v-loading="loading" class="height-auto">
      <el-aside>
        <el-menu :default-active="activeMenu" @select="menuChange">
          <el-menu-item v-for="(item,key) of menu" :key="key" :index="item">{{item}}</el-menu-item>
        </el-menu>
      </el-aside>
      <el-main>
        <el-input placeholder="本页内筛选" v-model="search" @input="searchChange" clearable></el-input>
        <el-collapse v-if="activeDict&&activeDict.length>0">
          <el-collapse-item v-for="(item,key) of activeDict" :key="key">
            <div slot="title">
              <el-tag v-for="(i,k) of item.match" :key="k" style="margin-right: 10px">{{i}}</el-tag>
            </div>
            <el-row>
              <span>描述: </span><p>{{item.describe}}</p>
            </el-row>
            <el-row>
              <el-tag size="small" :type="item.startWith?'success':'info'">匹配开头</el-tag>
              <el-tag size="small" :type="item['needReplace']?'success':'info'">去除匹配字符</el-tag>
            </el-row>
          </el-collapse-item>
        </el-collapse>
        <el-row v-else style="text-align: center;font-size: 14px;color: #8c939d">
          <p>无数据</p>
        </el-row>
      </el-main>
    </el-container>
  </el-card>
</template>

<script>
  export default {
    name: "plugins-dict",
    data() {
      return {
        menu: [],
        loading: true,
        dict:{},
        activeMenu:'',
        activeDict:[],
        search:''
      }
    },
    methods: {
      menuChange(index,indexPath){
        this.activeDict = this.dict[index]
      },
      searchChange(val){
        if (val==='') this.activeDict = this.dict[this.activeMenu]
        else this.activeDict=this.dict[this.activeMenu].filter(o=>{
          return o.match.some(obj=>{
            return obj.indexOf(val)>-1
          })
        })
      }
    },
    mounted() {
    },
    created() {
      this.$ajax({url: '/getMatchDict.json'}).then(res => {
        if (res['code'] === 0) {
          let data = res.data
          this.menu = Object.keys(data)
          this.dict = res.data
          if (this.menu.length>0) {
            this.activeMenu = this.menu[0]
            this.activeDict = this.dict[this.activeMenu]
          }
          this.loading = false
        } else {
          this.$message.error(res['msg'])
        }
      }).catch(err=>{
        this.$message.error(err)
      })
    }
  }
</script>

<style scoped>
  .height-auto{
    overflow:auto;
    max-height:840px;
    min-height: 400px;
  }
</style>



